
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import {mavonEditor} from 'mavon-editor';
import 'mavon-editor/dist/css/index.css';
export default {
name:"edit",
props:['id'],
components:{
XButton,
Group ,
XTextarea,
mavonEditor
},
data() {
return {
topics:{
title:"",
content:""
}
}
},
methods: {

editTopic(){
var app = this;
this.$http.post("/topics/update",{
accesstoken: this.$store.state.accesstoken,
topic_id : this.topics.id,
title: this.topics.title,
content:this.topics.content,
tab:"dev"
}).then(function(res){
console.log(res.data);
if(res.data.success){
app.$router.back();
}
})
}
},


beforeRouteEnter (to, from, next) {
next(function(vm){
vm.$http.get("/topic/"+to.params.id,{
params:{
accesstoken:vm.$store.state.accesstoken
}
}).then(function(res){
console.log(res.data)
if(res.data.success){
vm.topics = res.data.data;
}
})
})
},
beforeRouteUpdate (to, from, next) {
var app = this;
this.$http.get("/topic/"+to.params.id,{
params:{
accesstoken:this.$store.state.accesstoken
}
}).then(function(res){
if(res.data.success){
app.topics = res.data.data;
}
})
next();
}
}



