import request from "../ajax";
const actions = {
    actionsMessageCount(context){
        return request.get("/message/count",{
          params:{
            accesstoken:context.state.accesstoken
          }
        }).then(function(res){
          if(res.data.success){
            context.commit('mutationsCount',res.data.data) ;
          }
        })
      }
};

export default actions;


