
import { mapMutations } from "vuex";
import Msg from 'vux/src/components/msg/index.vue'

export default {
name:"logout",

methods: {
...mapMutations(['mutationsCountEmpty'])
},
data() {
return {
buttons:[
{
text:"跳转至登录",
type:"primary",
link:"/login"
}
]
}
},
components:{
Msg
},
created(){

if(this.$store.state.isLogined == false){
return;
}


window.localStorage.clear();

this.$store.commit("mutationsClear");

this.$router.push("/topiclist/all");

this.mutationsCountEmpty()
}
}



